<template>
  <div v-loading="loading">
    <cms-breadcrumb to="/shopManage/list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse="true">
        <li>营业中表示店铺正常使用中；暂停中表示商家可登录商家中心，但无法上架商品；关闭中表示店铺无法登陆商家中心</li>
        <li>开启商品审核机制表示店铺上架商品需要经过平台审核，特别注意：此状态只有在平台商城设置中未开启商家上架商品审核机制的基础上才能生效</li>
      </cms-prompt>
      <div style="margin:15px 0;">
        <div class="wx-userinfo">
          <img src="/static/loading.png" alt class="wx-userinfo--avatar">
          <div class="wx-userinfo--content">
            <p class="wx-userinfo--name">
              <span>{{formData.storeName}}</span>
              <el-tag size="small" type="success" v-if="formData.status==1">正常营业</el-tag>
              <el-tag size="small" type="warning" v-if="formData.status==2">暂停营业</el-tag>
              <el-tag size="small" type="info" v-if="formData.status==3">禁止营业</el-tag>
              <el-tag size="small" type="success" v-if="formData.isProductAuditEnable">开启商品审核机制</el-tag>
              <el-tag size="small" type="info" v-else>开启商品审核机制</el-tag>
            </p>
            <p class="wx-userinfo--info">店铺账号: {{formData.storeUsername}}</p>
            <p class="wx-userinfo--info">店铺类型: {{formData.storeType==1?'个人店铺':'企业店铺'}}</p>
            <p class="wx-userinfo--info">操作:
              <el-button
                size="mini"
                type="primary"
                @click="setStatus(1)"
                v-if="formData.status!=1"
              >开启营业</el-button>
              <el-button
                size="mini"
                type="warning"
                @click="setStatus(2)"
                v-if="formData.status!=2"
              >暂停营业</el-button>
              <el-button
                size="mini"
                type="info"
                @click="setStatus(3)"
                v-if="formData.status!=3"
              >禁止营业</el-button>
              <el-button
                size="mini"
                type="primary"
                @click="setAudit(true)"
                v-if="!formData.isProductAuditEnable"
              >开启商品审核</el-button>
              <el-button size="mini" type="info" @click="setAudit(false)" v-else>取消商品审核</el-button>
            </p>
          </div>
        </div>
      </div>
      <el-form :model="formData" label-width="300px" v-if="!loading">
        <h3 class="cms-segmentation">店铺信息</h3>
        <el-form-item label="开店会员:">{{formData.storeUsername}}</el-form-item>
        <el-form-item label="联系人手机号:">{{formData.contactMobile}}</el-form-item>
        <el-form-item label="联系人邮箱:">{{formData.contactMail}}</el-form-item>
        <el-form-item label="联系人姓名:">{{formData.contactName}}</el-form-item>
        <h3 class="cms-segmentation">店铺经营信息</h3>
        <el-form-item
          label="主营类目:"
        >{{formData.productCategory?formData.productCategory.cateName:'暂无'}}</el-form-item>
        <div v-if="formData.storeType===2">
          <h3 class="cms-segmentation">公司信息</h3>
          <el-form-item label="公司名称:">{{formData.storeInfo.companyName}}</el-form-item>
          <el-form-item label="公司所在地:">
            {{formData.storeInfo.provinceName}}
            {{formData.storeInfo.cityName}}
            {{formData.storeInfo.areaName}}
          </el-form-item>
          <el-form-item label="详细地址:">{{formData.storeInfo.address}}</el-form-item>
          <el-form-item label="公司电话:">{{formData.storeInfo.mobile}}</el-form-item>
          <h3 class="cms-segmentation">营业执照</h3>
          <el-form-item required label="营业执照注册号:">{{formData.storeInfo.blNo}}</el-form-item>
          <el-form-item required label="营业执照所在地:">
            {{formData.storeInfo.blProvinceName}}
            {{formData.storeInfo.blCityName}}
            {{formData.storeInfo.blAreaName}}
          </el-form-item>
          <el-form-item required label="营业期限:">
            {{$moment(formData.storeInfo.blStartTime,'YYYY-MM-DD')}} 至
            {{$moment(formData.storeInfo.blEndTime,'YYYY-MM-DD')}}
          </el-form-item>
          <el-form-item required label="注册资本:">{{formData.storeInfo.registeredCapital}}万元</el-form-item>
          <el-form-item required label="成日日期:">{{$moment(formData.storeInfo.setupDate)}}</el-form-item>
          <el-form-item required label="法人姓名:">{{formData.storeInfo.legalName}}</el-form-item>
          <el-form-item required label="身份证号:">{{formData.storeInfo.legalIdcard}}</el-form-item>
          <el-form-item required label="身份证正面照片:">
            <cms-picture-zone :data="formData.storeInfo.idcardPositivePhotoRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item required label="身份证反面照片:">
            <cms-picture-zone :data="formData.storeInfo.idcardReversePhotoRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item required label="手持身份证照:">
            <cms-picture-zone :data="formData.storeInfo.photoRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item required label="营业执照证书:">
            <cms-picture-zone :data="formData.storeInfo.blCertRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item required label="经营范围:">{{formData.storeInfo.scopeRange}}</el-form-item>
          <h3 class="cms-segmentation">税务登记</h3>
          <el-form-item required label="税务登记代码:">{{formData.storeInfo.taxRegistrationCode}}</el-form-item>
          <el-form-item required label="营业执照证书:">
            <cms-picture-zone :data="formData.storeInfo.taxRegistrationCertRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
        </div>
        <div v-else>
          <h3 class="cms-segmentation">身份信息</h3>
          <el-form-item required label="姓名:">{{formData.storeInfo.legalName}}</el-form-item>
          <el-form-item required label="身份证号:">{{formData.storeInfo.legalIdcard}}</el-form-item>
          <el-form-item required label="身份证正面照片:">
            <cms-picture-zone :data="formData.storeInfo.idcardPositivePhotoRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item required label="身份证反面照片:">
            <cms-picture-zone :data="formData.storeInfo.idcardReversePhotoRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item required label="手持身份证照:">
            <cms-picture-zone :data="formData.storeInfo.photoRes" :disabled="true"></cms-picture-zone>
          </el-form-item>
        </div>
        <div >
          <h3 class="cms-segmentation">财务信息</h3>
          <el-form-item label="结算账户类型:" required>支付宝</el-form-item>
          <el-form-item label="支付宝姓名:" required>
            <span v-if="formData.storeSettlementAccountList.length==0">-</span>
            <span
              v-if="formData.storeSettlementAccountList.length>0"
            >{{formData.storeSettlementAccountList[0].accountName}}</span>
          </el-form-item>
          <el-form-item label="支付宝账号:" required>
            <span v-if="formData.storeSettlementAccountList.length==0">-</span>
            <span
              v-if="formData.storeSettlementAccountList.length>0"
            >{{formData.storeSettlementAccountList[0].account}}</span>
          </el-form-item>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import {
  shopManageGet,
  setShopStatus,
  shopProductAudit,
  shopWeChatEnable
} from '@/api/shop/shopManage.js'
var defaultData = {}
export default {
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      accountList: {
        account: '',
        accountName: ''
      }
    }
  },
  methods: {
    getStoreInfo(id) {
      // 获取商家信息
      this.loading = true
      shopManageGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
          if (res.data.storeSettlementAccountList) {
            this.accountList = res.data.storeSettlementAccountList.filter(
              v => v.isDefault === true
            )[0]
          }
          this.loading = false
        }
      })
    },
    setWeChat(status) {
      let info = ''
      if (status) {
        info = `开启微信设置商家即可在商家后台单独设置属于自己的公众号及小程序，提别注意：在平台商城设置中选择部分开启模式才能生效，确认是否继续？`
      } else {
        info = `取消微信设置商家无法在商家后台单独设置属于自己的公众号及小程序，提别注意：在平台商城设置中选择部分开启模式才能生效，确认是否继续?`
      }
      this.$confirm(info, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          shopWeChatEnable({ id: this.formData.id, wechatenable: status }).then(
            res => {
              if (res.code === 200) {
                this.$successMessage('操作成功')
                this.getStoreInfo(this.$route.query.id)
              } else {
                this.$errorMessage(res.message)
              }
            }
          )
        })
        .catch(() => {})
    },
    setAudit(status) {
      // 开启关闭商品审核机制
      let info = ''
      if (status) {
        info = `开启后店铺上架商品需要经过店铺审核后才能上架，特别注意：只有在平台商城设置中未开启商家上架商品审核机制的基础上才能生效，确认是否继续？`
      } else {
        info = `取消后店铺上架商品无需经过店铺审核后就能上架，特别注意：只有在平台商城设置中未开启商家上架商品审核机制的基础上才能生效，确认是否继续`
      }
      this.$confirm(info, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          shopProductAudit({ id: this.formData.id, productaudit: status }).then(
            res => {
              if (res.code === 200) {
                this.$successMessage('操作成功')
                this.getStoreInfo(this.$route.query.id)
              } else {
                this.$errorMessage(res.message)
              }
            }
          )
        })
        .catch(() => {})
    },
    setStatus(type) {
      // 修改店铺营业状态
      let info = ''
      if (type === 1) {
        info = `店铺当前为${
          this.formData.storeName
        }，确认是否开启店铺的正常营业?`
      }
      if (type === 2) {
        info = `店铺当前为${
          this.formData.storeName
        }，暂停后店铺所有商品将无法在商城展示但依然可登录商家中心，确认是否继续?`
      }
      if (type === 3) {
        info = `店铺当前为${
          this.formData.storeName
        }，禁止后店铺将无法经营且不能登录商家中心，确认是否继续?`
      }

      this.$confirm(info, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          setShopStatus({ id: this.formData.id, status: type }).then(res => {
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.getStoreInfo(this.$route.query.id)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    }
  },
  created() {
    this.formData.storeType = this.$route.query.storeType
    this.getStoreInfo(this.$route.query.id)
  }
}
</script>
<style>
</style>



